.music-grid{
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-evenly;
  flex-wrap: nowrap;
}

.subscription-grid{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: grey;
  border: 1px solid;
  max-width:50vw;
  min-width:45vw;
  display: flex;
  flex-flow: row wrap;
  border-radius: 20px;
  
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}

.query-grid{
